<template>
  <v-container>
    <v-row>
      <v-col cols="7">
        <d3-network
         :net-nodes="persons"
         :net-links="links"
         :options="options" />
      </v-col>
      <v-col cols="5">
        <v-row>
          <v-col cols="12">
            <mbti-legends
              :labels="compatibilityLabels"
              :colors="matchColors"
            ></mbti-legends>
          </v-col>
        </v-row>
        <person-create
          v-on:add-person="addPerson"
        ></person-create>
        <v-divider></v-divider>
        <v-row>
          <v-col cols="6" align="left">
            <v-btn color="primary" class="ma-2" @click="doCopy">
              Copy URL
            </v-btn>
          </v-col>
          <v-col cols="6" align="right">
            <v-switch
              class="ma-2" 
              v-model="showLinkLabel"
              label="Compatibility"
            ></v-switch>
          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <person-list
              v-bind:persons="persons"
              v-on:remove-person="removePerson"
            ></person-list>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import D3Network from 'vue-d3-network'
import PersonList from './PersonList'
import PersonCreate from './PersonCreate'
import MbtiLegends from './MbtiLegends'

const mbtiIndex = {
  'ENTJ': 0, 'ENTP': 1, 'INTJ': 2, 'INTP': 3, 
  'ESTJ': 4, 'ESFJ': 5, 'ISTJ': 6, 'ISFJ': 7,
  'ENFJ': 8, 'ENFP': 9, 'INFJ': 10, 'INFP': 11,
  'ESTP': 12, 'ESFP': 13, 'ISTP': 14, 'ISFP': 15,
}

const mbtiScores = [
  [1, 2, 3, 3, 2, 1, 2, 1, 2, 3, 3, 5, 4, 4, 3, 5],
  [2, 4, 3, 3, 4, 4, 5, 5, 4, 2, 4, 4, 2, 1, 2, 1],
  [3, 3, 3, 2, 2, 1, 3, 1, 3, 3, 3, 4, 5, 5, 3, 4],
  [3, 3, 2, 3, 4, 5, 3, 5, 5, 3, 4, 2, 2, 1, 2, 1],
  
  [2, 4, 2, 4, 3, 2, 3, 3, 1, 3, 1, 5, 1, 3, 3, 5],
  [1, 4, 1, 5, 2, 3, 3, 3, 3, 4, 2, 3, 3, 2, 5, 3],
  [2, 5, 3, 3, 3, 3, 3, 2, 1, 5, 1, 4, 3, 3, 2, 4],
  [1, 5, 1, 5, 3, 3, 2, 2, 2, 5, 2, 3, 3, 3, 4, 1],

  [2, 3, 3, 5, 1, 3, 1, 2, 4, 2, 3, 3, 4, 4, 5, 3],
  [3, 2, 3, 3, 4, 4, 5, 5, 2, 3, 3, 2, 1, 1, 1, 3],
  [3, 3, 3, 4, 1, 2, 1, 2, 3, 3, 3, 1, 5, 5, 5, 3],
  [5, 3, 4, 2, 5, 3, 4, 3, 3, 2, 1, 3, 1, 2, 2, 2],

  [4, 2, 5, 2, 1, 3, 3, 3, 4, 1, 5, 1, 3, 2, 3, 3],
  [4, 1, 5, 1, 3, 2, 3, 3, 4, 1, 5, 2, 2, 2, 3, 3],
  [3, 2, 3, 2, 3, 5, 2, 4, 5, 1, 5, 1, 3, 3, 2, 2],
  [5, 1, 4, 1, 5, 3, 4, 1, 3, 3, 3, 2, 3, 3, 2, 3],
]

const matchColors = [
  'lighgray', 'red', 'orange', 'lightgreen', 'green', 'blue'
]

const compatibilityLabels = [
  '', '최악', '다소 불편', '불편하지는 않음', '친해지기 쉬움', '마음 잘 맞음'
]

var STORAGE_KEY = "mbti-chart-1.0";
var personStorage = {
  fetch: function() {
    var persons = JSON.parse(localStorage.getItem(STORAGE_KEY) || "[]")
    persons.forEach(function(person, index) {
      person.id = index
    })
    personStorage.uid = persons.length
    return persons
  },
  save: function(persons) {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(persons))
  }
}



export default {
  name: 'home',
  components: {
    D3Network,
    PersonList,
    PersonCreate,
    MbtiLegends,
  },
  data() {
    return {
      nodeSize: 30,
      canvas: false,
      persons: personStorage.fetch(),
      compatibilityLabels,
      matchColors,
      showLinkLabel: false,
    }
  },
  mounted: function() {
    var data = getParam('persons')
    if (data != '') {
      var decoded = JSON.parse(decodeURIComponent(window.atob(data)))
      this.loadPersons(decoded)
    }
  },
  watch: {
    persons: {
      handler: function(persons) {
        personStorage.save(persons)
      },
      deep: true
    }
  },
  computed: {
    options() {
      return {
        force: 9000,
        size: {
          w: 600, h: 600
        },
        nodeSize: this.nodeSize,
        nodeLabels: true,
        linkLabels: true,
        linkWidth: 4,
        canvas: this.canvas
      }
    },
    links: function() {
      var links = []
      let size = this.persons.length
      for (var i = 0; i < size - 1; i++) {
        for (var j = i + 1; j < size; j++) {
          let score = getMatchScore(this.persons[i], this.persons[j])
          links.push({
            sid: this.persons[i].id, 
            tid: this.persons[j].id,
            _color: getColor(score),
            name: this.getLinkLabel(score),
            })
        }
      }
      return links
    },
    shareUrl: function() {
      var host = window.location.protocol + '//' + window.location.host
      return host + '?persons=' + exportPersons(this.persons)
    }
  },
  methods: {
    addPerson(mbtiType, name) {
      this.persons.push({
        id: personStorage.uid++,
        mbtiType: mbtiType,
        name: name.trim(),
      });
    },
    removePerson(person) {
      this.persons.splice(this.persons.indexOf(person), 1)
    },
    loadPersons(persons) {
      this.resetPersons()
      persons.forEach(person => {
        this.addPerson(person['t'], person['n'])
      })
    },
    resetPersons() {
      this.persons.splice(0, this.persons.length)
    },
    doCopy: function() {
      this.$copyText(this.shareUrl).then(function() {
      }, function() {
        alert('cant not copy')
      })
    },
    getLinkLabel: function(score) {
      if (!this.showLinkLabel) {
        return ''
      }
      return compatibilityLabels[score]
    }
  }
}

function getMatchScore(node1, node2) {
  let idx1 = mbtiIndex[node1.mbtiType]
  let idx2 = mbtiIndex[node2.mbtiType]
  return mbtiScores[idx1][idx2]
}

function getColor(score) {
  return matchColors[score]
}

function exportPersons(persons) {
  var ps = persons.map(person => {
    return { n: person.name, t: person.mbtiType }
  })
  var data = encodeURIComponent(JSON.stringify(ps))
  return window.btoa(data)
}

function getParam(sname) {
    var params = location.search.substr(location.search.indexOf("?") + 1);
    var sval = ""
    params = params.split("&")
    for (var i = 0; i < params.length; i++) {
        var temp = params[i].split("=")
        if ([temp[0]] == sname) {
          sval = temp[1]
        }
    }
    return sval
}


</script>
<style src="vue-d3-network/dist/vue-d3-network.css"></style>
<style>
.node-label {
  font-size: 20px !important;
  font-weight: 700;
}
</style>