<template>
    <v-form ref="form" v-on:submit="addPerson">
        <v-row align="center">
            <v-col class="d-flex" cols="4">
                <v-select
                v-model="mbtiType"
                :items="mbtiTypes" 
                label="MBTI Type"
                required
                ></v-select>
            </v-col>
            <v-col class="d-flex" cols="5">
                <v-text-field
                v-model="name"
                label="Name"
                required
                ></v-text-field>
            </v-col>
            <v-col class="d-flex" cols="3">
                <v-btn large color="primary" type="submit">Add</v-btn>
            </v-col>
        </v-row>
    </v-form>
</template>

<script>
const mbtiTypes = [
  'ENFJ', 'ENFP', 'ENTJ', 'ENTP',
  'ESFJ', 'ESFP', 'ESTJ', 'ESTP',
  'INFJ', 'INFP', 'INTJ', 'INTP',
  'ISFJ', 'ISFP', 'ISTJ', 'ISTP',
]
export default {
    data() {
        return {
            mbtiTypes: mbtiTypes,
            name: '',
            mbtiType: '',
        }
    },
    methods: {
        addPerson(event) {
            event.preventDefault()

            if (this.name != '' && this.mbtiType != '') {
                const name = this.name
                const mbtiType = this.mbtiType
                this.$emit('add-person', mbtiType, name)
                
                this.$refs.form.reset()
                this.name = ''
                this.mbtiType = ''
            }
        },
    },
}
</script>